.friendMessage {
}

.friendImageAndText {
    display: flex;
    gap: 14px;

}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    align-self: center;

}

.friendText {
    background: #F5F7FB;
    padding: 12px 13px 12px 27px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    border-radius: 8px;
    gap: 6px;
}

.friendText:after {

}

.friendName {
    font-weight: 600;
    font-size: 18px;


}

.friendMessageText {
    font-weight: 400;
    font-size: 16px;

}

.friendTime {
    font-family: Montserrat;
    font-weight: 600;
    font-size: 10px;
    line-height: 100%;
    letter-spacing: 0;
    margin-left:12px ;

}
