* {
    font-family: Montserrat, sans-serif !important;
}

.sendForm {
    display: flex;
    flex-direction: row;
    gap: 44px;
    width: 100%;
    margin-top: 50px;
}

.textarea {
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    width: 964px;
    border-radius: 20px;
    background: #F5F7FB;
    border: none;
    min-height: 40px;
    resize:none;
    padding: 12px 24px;
}

.button {
    border-radius: 20px;
    max-width: 120px;
    padding: 10px 40px;
    border: none;
    background: rgba(0, 102, 204, 1);
    color: #FFFFFF;
}

.button:hover {
cursor: pointer;
}

.button:active {
    transform:scale(1.05);
}
