.message {
}

.imageAndText {
    display: flex;
    gap: 14px;
    justify-content: flex-start;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    align-self: center;
}

.text {
    background: rgba(0, 102, 204, 1);
    color: white;
    padding: 12px 13px 12px 27px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
    border-radius: 8px;
    gap: 6px;
}

.text:after {

}

.name {
    font-weight: 600;
    font-size: 18px;
}

.messageText {
    font-weight: 400;
    font-size: 16px;
}

.time {
    font-weight: 600;
    font-size: 10px;
    line-height: 100%;
    letter-spacing: 0;
    margin-left: 12px;
    justify-self: flex-end;
    margin-right: 12px
}